.container{background-color:#b4dbfe;color:#59585f}.container .login_content,.container .login_form,.container .login_inputs{display:grid}.container .login_content{position:relative;height:100vh;align-items:center}.container .login_content .logo{z-index:100;position:absolute;top:0;margin:25px}.container .login_content .logo img{width:180px}.container .login_content .login_img{position:absolute;width:100%;height:100%;object-fit:fill;object-position:center}.container .login_content .login_img img{max-width:100%;height:auto}.container .login_content .login_form{position:absolute;right:0;background-color:rgba(247,247,247,.781);border:2px solid rgba(232,231,238,.75);margin-inline:1.5rem;row-gap:1.25rem;backdrop-filter:blur(20px);padding:2rem;border-radius:.5rem;width:45%}.container .login_content .login_form .login_title{color:#1b1b22;font-size:1.9rem;margin-bottom:.5rem}.container .login_content .login_form .login_title span{color:#e82f27}.container .login_content .login_form .login_description{font-size:.813rem}.container .login_content .login_form .login_inputs{row-gap:.75rem;margin-bottom:.5rem}.container .login_content .login_form .login_inputs .login_input{width:100%;padding:14px 12px;border-radius:6px;border:2px solid #59585f;background-color:rgba(232,231,238,.6);color:#1b1b22;font-size:.75rem;font-weight:500;transition:border .4s}.container .login_content .login_form .login_inputs .login_input::placeholder{color:#59585f}.container .login_content .login_form .login_inputs .login_input:focus,.container .login_content .login_form .login_inputs .login_input:valid{border:2px solid #e82f27}.container .login_content .login_form .login_label{display:block;color:#1b1b22;font-size:.813rem;font-weight:600;margin-bottom:.25rem}.container .login_content .login_form .login_box{position:relative}.container .login_content .login_form .login_box .login_input{padding-right:36px}.container .login_content .login_form .login_box .login_eye{width:max-content;height:max-content;position:absolute;right:.75rem;top:0;bottom:0;margin:auto 0;font-size:1.25rem;cursor:pointer}.container .login_content .login_form .login_check{display:flex;column-gap:.5rem;align-items:center}.container .login_content .login_form .login_check_input{appearance:none;width:16px;height:16px;border:2px solid #59585f;background-color:rgba(232,231,238,.2);border-radius:.25rem}.container .login_content .login_form .login_check_input:checked::before{content:"✔️";display:block;color:#fff;font-size:.75rem;transform:translate(-2px, -4px)}.container .login_content .login_form .login_check_label{font-size:.813rem;font-weight:600}.container .login_content .login_form .login_buttons{display:flex;column-gap:.75rem}.container .login_content .login_form .login_buttons .login_button{width:100%;padding:14px 2rem;border-radius:6px;background:linear-gradient(180deg, #E82F27, #BF2A2A);color:#fff;font-size:.813rem;font-weight:600;box-shadow:0 6px 24px rgba(43,31,214,.5);margin-bottom:1rem;cursor:pointer}.container .login_content .login_form .login_buttons .login_button_ghost{width:100%;padding:14px 2rem;border-radius:6px;font-size:.813rem;font-weight:600;box-shadow:0 6px 24px rgba(43,31,214,.5);margin-bottom:1rem;cursor:pointer;background-color:rgba(232,231,238,.6);border:2px solid #e82f27;color:#e82f27;box-shadow:none}.container .login_content .login_form .login_buttons .login_button_ghost a{text-decoration:none;color:#e82f27}.container .login_content .login_form .login_forgot{font-size:.75rem;font-weight:600;color:#e82f27;text-decoration:none}.container .login_content input,.container .login_content button{font-family:"Poppins";border:none;outline:none}